<template>
  <div id="app">
    <section v-if="errored">
      <p>We're not able to retrieve data from the API at this moment. The following reason was given: <span class="error">{{ error_msg }}</span></p>
      <p>Fallback data will be used for the game.</p>
    </section>

    <section v-else>
      <div v-if="loading">Loading...</div>
    </section>
    <hr>
    <Header />
    <!-- <img alt="Vue logo" src="./assets/logo.png"> -->
    <Deck 
      v-bind:deckOfCards="deckOfCards" 
      v-on:del-card="deleteCard" 
      v-on:shuffle-cards="shuffleCards" 
      v-on:reset-card="resetSelection" 
      v-on:check-selected-cards="checkSelectedCards" 
    />   
  </div>
</template>

<script>
import Header from './components/layout/Header.vue'
import localAssets from './assets/images.json'
// import Todos from './components/Todos.vue'
// import Cards from './components/Cards.vue'
import Deck from './components/Cards.vue'

// Vendor
import {_} from 'vue-underscore'
import axios from 'axios'

// Used to reset the data to resart the game
function initCardDeck() {
  return [
    {
      id: 0,
      title: "Ace",
      selected: false,
      disabled: 0,
      matched: false
    },
    {
      id: 1,
      title: "King",
      selected: false,
      disabled: 0,
      matched: false
    },
    {
      id: 2,
      title: "Queen",
      selected: false,
      disabled: 0,
      matched: false
    },
    {
      id: 3,
      title: "Jack",
      selected: false,
      disabled: 0,
      matched: false
    }
  ] 
}
export default {
  name: 'app',
  components: {
    Deck,
    Header
  },
  data() {
    return {
      deckOfCards: initCardDeck(),
      api_data: null,
      assets:  null,
      loading: true,
      errored: false,
      error_msg: null
    }
  },
  methods: {
    resetDeck: function (){
      console.log('resetDeck')
      this.deckOfCards = [];
      Object.assign(this.deckOfCards, initCardDeck());
    },
    deleteCard(id){
      console.log('fired deleteCard with ID:'+id);
      // return stack of cards without the selected card
      this.deckOfCards = this.deckOfCards.filter(card => card.id !== id);
    },
    shuffleCards(){
      console.log('fired shuffleCards');
      this.resetDeck();

      let deck = this.deckOfCards.concat(this.deckOfCards);
      for (var i = 0; i < deck.length; i++) {
        deck[i] = {
          id: i,
          title: deck[i].title,
          selected: deck[i].selected,
          disabled: deck[i].disabled,
          matched: deck[i].matched
        }
      }
      // console.log('new deck',deck);
      console.log('shuffled deck',_.shuffle(deck));
      this.deckOfCards = _.shuffle(deck);

      // return _.shuffle(deck);
    },
    checkSelectedCards(){
      console.log('fired checkSelectedCards');
      let choices = _.where(this.deckOfCards,{selected: true})
      console.log('choices: ',choices);
      if (choices[0].title === choices[1].title) {
        alert('Matched Pair Found!!')
        let matchedPairs = _.where(this.deckOfCards,{title: choices[0].title})

        for (var i = 0; i < matchedPairs.length; i++) {
          let targetCard = _.findWhere(this.deckOfCards,{id: matchedPairs[i].id})
          // console.log('targetCard',targetCard)
          targetCard.matched = true
          targetCard.disabled = 1
        }
        setTimeout(this.resetSelection, 1000)
      }else{
        alert(':( Try again')
        setTimeout(this.resetSelection, 1000)
      }
    },
    resetSelection() {
      _.findIndex(this.deckOfCards, {matched: false}) < 0 ? alert('GAME OVER') : console.log('game not over')
      for (var i = 0; i < this.deckOfCards.length; i++) {
        console.log(this.deckOfCards[i]);
        this.deckOfCards[i].selected = false
      }
      // this.deckOfCards = _.shuffle(deck);
    }
  },
  watch: {
    deckOfCards: function(newValue, oldValue) {
      console.log('Watch fired')
      console.log('newValue: ',newValue)
      console.log('oldValue: ',oldValue)
    }
  },
  mounted () {
    console.log('App mounted now');
    // Try to fetch data, if error use fallback local data, show Error template message
    // Web test ASSETS 
    axios
      .get('https://evtask.t12y.net/assets')
      .then(response => {
        this.assets = response
      })
      .catch(error => {
        console.log(error)
        this.errored = true
        this.error_msg = error
        this.assets = localAssets
      })
      .finally(() => this.loading = false)
    this.shuffleCards()
  },
  created() {
    console.log('App created now');
  }
}
</script>

<style scoped>
blockquote {
  background: black;
  color: gray;
  font-family: Times;
  padding: 1em;
}
.error{
  padding: 2px 5px;
  color: white;
  background-color: red;
}
</style>
